import { send } from "@giantmachines/redux-websocket/dist";
import { WEBSOCKET_PREFIX } from "environ";
import { DefaultRootState } from "react-redux";
import { ThunkAction } from "redux-thunk";
import { v4 as uuid } from 'uuid';

export function wsSend(message: any): ThunkAction<string, DefaultRootState, any, any> {
    return function (dispatch, getState) {
        const requestId = uuid();
        dispatch( send( {requestId, sessionId: getState().app.login.sessionId, ...message}, WEBSOCKET_PREFIX ) );
        return requestId;
    }
}