////// Packages //////////////////
import { useDispatch } from 'react-redux';
import { useState } from 'react';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////
import storeFetch, { Outputtable } from '../store/helpers/storeFetch';
import { $api } from 'environ';
import { StoreDataReturn } from 'store/store';
import { ApiInterface } from 'store/types/interface/common';

////// Custom Hooks //////////////

////// Thunks ////////////////////





const useFetch = (timeout = 3000) => {
        const [loading, setLoading] = useState(false);
        const [error, setError] = useState('');
        const dispatch = useDispatch();

        const post = async <FetchActions extends (keyof ApiInterface)[]>(data: any, path = $api) => {
            setLoading(true);
            const errTimeout = setTimeout( () => {
                setLoading( false ); 
                setError( 'Request Timed Out' );
            }, timeout )
            const result = (await dispatch(storeFetch<FetchActions>(data, path, 'POST'))) as unknown as StoreDataReturn<Outputtable<FetchActions>>;
            setLoading(false);
            clearTimeout(errTimeout);
            if (!result?.success) {
                if (result?.error) {
                    setError(result.error)
                } else {
                    setError('Something went wrong')
                }
            }
            return result;
        }

        const get = async (path: string) => {
            setLoading(true);
            const errTimeout = setTimeout( () => {
                setLoading( false ); 
                setError( 'Request Timed Out' );
            }, timeout )
            const result = await dispatch(storeFetch(null, path, 'GET'))
            setLoading(false);
            clearTimeout(errTimeout);
            return result;
        }
        
        return {
            loading,
            error,
            post,
            get
        }

}


export default useFetch;